.foodCard {
    width: 100%;
    border-radius: 20px;
    padding: 40px 15px 20px 15px;
    margin-top: 40px;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    position: relative;
}

.foodCardInfo {
    color: white;
    width: 100%;
    background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.5) 0%,
        rgba(255, 255, 255, 0) 100%
    );
    backdrop-filter: blur(10px);
    border-radius: 20px;
    margin-top: 131px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
}

.shadow {
    position: absolute;
    border-radius: 20px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    top: 0;
    left: 0;
}

.cardProps {
    padding: 20px 10px 5px 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.cardProps p {
    font-size: 10px;
}

.elemProps {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}

.elemProps p {
    margin-left: 7px;
}
